import React, { Component, Fragment } from "react";
import { Helmet } from "react-helmet";
import { linkClicked } from "../side_navigationbar/Navbar";
import axios from "axios";
import $ from "jquery";
import "./Styling.css";
import { contactTracing } from "../../urls/url";

axios.defaults.xsrfHeaderName = "x-csrftoken";
axios.defaults.xsrfCookieName = "csrftoken";

// Styling property for Underline Image
const Underline = {
  width: "75px",
  height: "9px",
  position: "absolute",
};

class Assets extends Component {
  /** Method is called on Component Load */
  componentDidMount() {
    linkClicked(3);
  }

  /** Method to get contact tracing data */
  search = () => {
    $("#searchTable").empty();
    $("#tracing-error").text("");
    let id = $("#tagid").val();
    let startdate = $("#startdate").val();
    let enddate = $("#enddate").val();
    console.log(id + " " + startdate + " " + enddate);
    let data = {
      tagId: id,
      start: startdate + "-00:00:00",
      end: enddate + "-23:59:59",
    };
    if (id === "" || startdate === "" || enddate === "") {
      $("#tracing-error").text("Please enter all fields.");
    } else if (
      id.length !== 17 &&
      !id.match("([A-Za-z0-9]{2}[-]){5}([A-Za-z0-9]){2}")
    ) {
      $("#tracing-error").text("Invalid Tag Id entered.");
    } else {
      axios({ method: "POST", url: contactTracing, data: data })
        .then((response) => {
          console.log(response);
          if (response.status === 200 || response.status === 201) {
            let contacts = response.data.result;
            if (contacts.length !== 0) {
              for (let i = 0; i < contacts.length; i++) {
                $("#searchTable").append(
                  "<tr><td>" +
                    (1 + i) +
                    "</td><td>" +
                    contacts[i].tagId +
                    "</td><td>" +
                    contacts[i].name +
                    "</td><td>" +
                    contacts[i].eventStartTime.substring(0, 10) +
                    " " +
                    contacts[i].eventStartTime.substring(11, 19) +
                    "</td><td>" +
                    contacts[i].eventEndTime.substring(0, 10) +
                    " " +
                    contacts[i].eventEndTime.substring(11, 19) +
                    "</td><td>" +
                    contacts[i].eventCount +
                    "</td><td>" +
                    contacts[i].timestamp +
                    "</td></tr>"
                );
              }
            } else {
              $("#tracing-error").text("No Contact Tracing data found.");
            }
          }
        })
        .catch((error) => {
          console.log(error);
          if (error.response.status === 403) {
            $("#timeout_modal").css("display", "block");
            $("#content").text(
              "User Session has timed out. Please Login again"
            );
          } else {
            $("#tracing-error").text(
              "Request Failed with status code (" + error.response.status + ")."
            );
          }
        });
    }
    console.log(data);
  };

  /** Terminate the session on forbidden (403) error */
  sessionTimeout = () => {
    $("#timeout_modal").css("display", "none");
    sessionStorage.removeItem("isLoggedIn");
    this.props.handleLogin(0);
  };

  /** Redern the html content on the browser */
  render() {
    return (
      <Fragment>
        <Helmet>
          <title>Contact Tracing</title>
        </Helmet>
        <div className="panel">
          <span className="main-heading">CONTACT TRACING</span>
          <br />
          <img alt="" src="../images/Tiles/Underline.png" style={Underline} />

          <div className="container fading" style={{ marginTop: "50px" }}>
            <p className="error-msg" id="tracing-error"></p>
            <div className="row">
              {/* Input field for tag MAC ID */}
              <div className="input-group">
                <span className="label">Tag MAC ID : </span>
                <input
                  type="text"
                  id="tagid"
                  placeholder="5a-c2-15-00-00-00"
                  required="required"
                />
              </div>
              {/* Input fields for time interval */}
              <div className="input-group">
                <span className="label">Date : </span>
                <input type="date" id="startdate" required="required" />{" "}
                to&nbsp;
                <input type="date" id="enddate" required="required" />
              </div>
              {/* Button for searching tag */}
              <div className="input-group">
                <input
                  type="button"
                  value="Get Data"
                  onClick={this.search}
                  className="btn success-btn"
                />
                &nbsp;&nbsp;
                {/* Clearing search data and hiding search result table */}
                <input
                  type="button"
                  value="Clear"
                  onClick={() => {
                    $("#tracing-error").text("");
                    $("input[type=text]").val("");
                    $("input[type=date]").val("");
                  }}
                  className="btn success-btn"
                />
              </div>
            </div>
            <hr />
            <br></br>
            <div className="row" id="tracingBlock" style={{ display: "block" }}>
              <span className="heading">CONTACT TRACING INFORMATION</span>
              <br />
              <img
                src="../images/Tiles/Underline.png"
                alt=""
                style={{
                  width: "56px",
                  height: "3px",
                  marginTop: "2px",
                  position: "absolute",
                }}
              />
              <br></br>
              {/* Table to display alert msgs */}
              <table
                style={{
                  marginTop: "20px",
                  marginBottom: "30px",
                }}
              >
                <thead>
                  <tr>
                    <th>Sl. NO.</th>
                    <th>TAG MAC ID</th>
                    <th>STUDENT NAME</th>
                    <th>START TIME</th>
                    <th>END TIME</th>
                    <th>EVENT COUNT</th>
                    <th>DURATION</th>
                  </tr>
                </thead>
                <tbody id="searchTable"></tbody>
              </table>
            </div>
          </div>
        </div>
        {/* Display modal to display error messages */}
        <div id="timeout_modal" className="modal">
          <div className="modal-content">
            <p id="content" style={{ textAlign: "center" }}></p>
            <button
              id="ok"
              className="btn-center btn success-btn"
              onClick={this.sessionTimeout}
            >
              OK
            </button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default Assets;
