import React, { Component, Fragment } from "react";
import axios from "axios";
import "./Styling.css";
import $ from "jquery";
import { deleteAsset } from "../../urls/url";

class DeleteAsset extends Component {
  componentDidMount() {}

  /** Hides all error and success messages displayed on all button clicks */
  hide = () => {
    $("#delError").text("");
    $("#delSuccess").text("");
  };
  /** Unregister the registered tags */
  unregister = (e) => {
    this.hide();
    e.preventDefault();
    let id = $("#macid").val();
    if (id.length === 0)
      $("#delError").text("Please Enter MAC ID to delete student details.");
    else if (
      !id.match("([A-Za-z0-9]{2}[-]){5}([A-Za-z0-9]){2}") &&
      id.length !== 17
    )
      $("#delError").text("Invalid MAC ID entered.");
    else {
      axios({
        method: "POST",
        url: deleteAsset,
        data: { tagId: id },
      })
        .then((response) => {
          // console.log(response);
          if (response.status === 201 || response.status === 200) {
            $("#delSuccess").text("Student details deleted successfully.");
          } else {
            $("#delError").text("Unable to delete student details.");
          }
        })
        .catch((error) => {
          // console.log(error);
          if (error.response.status === 403) {
            $("#timeout_modal3").css("display", "block");
            $("#content").text(
              "User Session has timed out. Please Login again"
            );
          } else {
            $("#delError").text(
              "Request Failed with status code (" + error.response.status + ")."
            );
          }
        });
    }
    $("input[type=text]").val("");
  };

  /** Terminate the session on forbidden (403) error */
  sessionTimeout = () => {
    $("#timeout_modal3").css("display", "none");
    sessionStorage.removeItem("isLoggedIn");
    this.props.handleLoginError();
  };

  render() {
    return (
      <Fragment>
        <span className="sub-heading">Delete Student</span>
        <br />
        <img
          src="../images/Tiles/Underline.png"
          alt=""
          style={{
            width: "56px",
            height: "3px",
            marginTop: "2px",
            position: "absolute",
          }}
        />
        <br></br>
        <div>
          {/* Element for displaying error messages */}
          <span className="error-msg" id="delError"></span>
          <span className="success-msg" id="delSuccess"></span>
        </div>
        {/* Form for deleting the registered tags */}
        <form id="delete-form" className="fading">
          {/* Input Field for Tag MAC ID */}
          <div className="input-group">
            <span className="label">Tag MAC ID :</span>
            <input
              type="text"
              name="macid"
              id="macid"
              required="required"
              onClick={this.hide}
              placeholder="5a-c2-15-00-00-00"
            />
          </div>
          <div className="input-group">
            <input
              type="submit"
              value="Delete Tag"
              onClick={this.unregister}
              className="btn success-btn"
            />
          </div>
        </form>
        {/* Display modal to display error messages */}
        <div id="timeout_modal3" className="modal">
          <div className="modal-content">
            <p id="content" style={{ textAlign: "center" }}></p>
            <button
              id="ok"
              className="btn-center btn success-btn"
              onClick={this.sessionTimeout}
            >
              OK
            </button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default DeleteAsset;
