import React, { Component, Fragment } from "react";
import { Helmet } from "react-helmet";
import { linkClicked } from "../side_navigationbar/Navbar";
import axios from "axios";
import "./Styling.css";
import $ from "jquery";
import RegAsset from "./RegAsset";
import RegGateway from "./RegGateway";
import UpdateAsset from "./UpdateAsset";
import DeleteAsset from "./DeleteAsset";
import BulkUpload from "./BulkUpload";

axios.defaults.xsrfHeaderName = "x-csrftoken";
axios.defaults.xsrfCookieName = "csrftoken";

// Styling property for Underline Image
const Underline = {
  width: "75px",
  height: "9px",
  position: "absolute",
};

const Navbtn = {
  // background: "white",
  padding: "8px 10px",
  border: "none",
  borderRadius: "4px",
  fontSize: "16px",
  cursor: "pointer",
  color: "Black",
  fontWeight: "bold",
  boxShadow: "3px 3px 5px 3px rgba(0, 0, 0, 0.25)",
};

/** Student configuration component which has three child component for;
 *  Student registration,
 *  Updating student details,
 *  Deleting Student details
 */
class Configuration extends Component {
  constructor() {
    super();
    this.state = { page: "1" };
  }

  /** Each time component is loaded the method will be called */
  componentDidMount() {
    linkClicked(1);
    $("#b1").css("color", "white");
    $("#b1").css("background", " rgb(0, 98, 135)");
  }

  /** Methods to display and hide the forms on button click  */
  // Displays Asset Rgistration Form
  showAssetForm = () => {
    this.setState({ page: "1" });
    $("#b1").css("color", "white");
    $("#b1").css("background", " rgb(0, 98, 135)");
    $("#b2").css("color", "black");
    $("#b2").css("background", "#efefef");
  };
  // Displays Master Gateway Rgistration Form
  showMasterForm = () => {
    this.setState({ page: "2" });
    $("#b2").css("color", "white");
    $("#b2").css("background", " rgb(0, 98, 135)");
    $("#b1").css("color", "black");
    $("#b1").css("background", "#efefef");
  };

  /** Redern the html content on the browser */
  render() {
    if (this.state.page === "1") {
      this.child = (
        <Fragment>
          <div className="container">
            <button
              className="col-3 heading"
              style={Navbtn}
              onClick={() => {
                $("#form1").css("display", "block");
                $("#form2").css("display", "none");
                $("#form3").css("display", "none");
              }}
            >
              Register
            </button>
            <button
              className="col-3 heading"
              style={Navbtn}
              onClick={() => {
                $("#form1").css("display", "none");
                $("#form2").css("display", "block");
                $("#form3").css("display", "none");
              }}
            >
              Update
            </button>
            <button
              className="col-3 heading"
              style={Navbtn}
              onClick={() => {
                $("#form1").css("display", "none");
                $("#form2").css("display", "none");
                $("#form3").css("display", "block");
              }}
            >
              Delete
            </button>
          </div>
          <br></br>
          <div id="form1" style={{ display: "block" }}>
            <RegAsset handleLoginError={this.sessionTimeout}></RegAsset>
          </div>
          <div id="form2" style={{ display: "none" }}>
            <UpdateAsset handleLoginError={this.sessionTimeout}></UpdateAsset>
          </div>
          <div id="form3" style={{ display: "none" }}>
            <DeleteAsset handleLoginError={this.sessionTimeout}></DeleteAsset>
          </div>
        </Fragment>
      );
    } else if (this.state.page === "2") {
      this.child = (
        <RegGateway handleLoginError={this.sessionTimeout}></RegGateway>
      );
    }
    return (
      <Fragment>
        <Helmet>
          <title>Configuration</title>
        </Helmet>
        <div className="panel">
          <span className="main-heading">CONFIGURATION</span>
          <br />
          <img alt="" src="../images/Tiles/Underline.png" style={Underline} />
          <div className="container fading" style={{ marginTop: "50px" }}>
            <div className="row">
              <button
                id="b1"
                className="col-2 heading"
                style={Navbtn}
                onClick={this.showAssetForm}
              >
                Asset Registration
              </button>
              <button
                id="b2"
                className="col-2  heading"
                style={Navbtn}
                onClick={this.showMasterForm}
              >
                Gateway Registration
              </button>
            </div>
            <div
              className="container"
              style={{
                borderRadius: "0px 0px 5px 5px",
                border: "2px solid rgb(2, 167, 140, 0.2)",
                boxShadow: "0px 0px 5px 1px grey",
                marginTop: "-2px",
                padding: "2% 10%",
              }}
              id="childComponent"
            >
              {this.child}
            </div>
            <div className="row">
              <BulkUpload></BulkUpload>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default Configuration;
