import React, { Component, Fragment } from "react";
import "./Header.css";
import axios from "axios";
import { logout } from "../../urls/url";

const Logo = {
  opacity: "0.6",
  width: "35px",
  float: "right",
  marginRight: "30px",
  cursor: "pointer",
};

// const Logout = {
//   opacity: "0.8",
//   float: "right",
//   color: "grey",
//   marginRight: "-40px",
//   marginTop: "20px",
//   fontFamily: "Roboto-Medium",
// };

class Header extends Component {
  logout = () => {
    axios({
      method: "GET",
      url: logout,
    })
      .then((response) => {
        sessionStorage.removeItem("isLoggedIn");
        this.props.handleLogin(0);
      })
      .catch((error) => {
        if (error.response.status === 403) {
          sessionStorage.removeItem("isLoggedIn");
          this.props.handleLogin(0);
        }
      });
  };

  render() {
    return (
      <Fragment>
        <div className="navbar">
          <div>
            <img alt="" src="../images/Tiles/Logo.png" />
            <img
              alt=""
              src="../images/Icons/Icon_Logout.png"
              style={Logo}
              title="Logout"
              onClick={this.logout}
              className="logoutImg"
            />
          </div>
        </div>
      </Fragment>
    );
  }
}

export default Header;
