import React, { Component, Fragment } from "react";
import { Helmet } from "react-helmet";
import { linkClicked } from "../side_navigationbar/Navbar";
import axios from "axios";
import $ from "jquery";
import "./Styling.css";
import { assetHealth, gatewayDetails } from "../../urls/url";

const Underline = {
  width: "75px",
  height: "9px",
  position: "absolute",
};

class SystemHealth extends Component {
  numberPerPage = 15;

  // variables for gateway table used for pagination
  asset_pageList = [];
  asset_currentPage = 1;
  asset_numberOfPages = 0;
  asset_currentRowCount = 0;

  // variables for assets table used for pagination
  gateway_pageList = [];
  gateway_currentPage = 1;
  gateway_numberOfPages = 0;
  gateway_currentRowCount = 0;

  /** Defining the states of the Component */
  constructor() {
    super();
    this.state = {
      gatewaylist: [],
      assetlist: [],
    };
  }

  componentDidMount() {
    linkClicked(4);
    this.gatewayHealth();
    this.assetHealth();
    // timer function for refreshing each 10 seconds
    this.interval_getGateway = setInterval(this.gatewayHealth, 15 * 1000);
    this.interval_getAssets = setInterval(this.assetHealth, 15 * 1000);
  }

  /** Displaying gateway health data in table format  */
  gatewayHealth = () => {
    axios({ method: "GET", url: gatewayDetails })
      .then((response) => {
        console.log(response);
        if (response.status === 200 || response.status === 201) {
          let data = response.data.gateways;
          if (data.length !== 0) {
            let currTime = new Date();
            let senC = 0;
            let lastseen;
            let gatewayArray = [];
            for (let i = 0; i < data.length; i++) {
              let status = "red";
              if (data[i].timestamp !== null) {
                lastseen =
                  data[i].timestamp.substring(0, 10) +
                  " " +
                  data[i].timestamp.substring(11, 19);
                let timestamp = new Date(
                  lastseen.substring(0, 10) + " " + lastseen.substring(11, 19)
                );
                if (currTime - timestamp <= 2 * 60 * 1000) status = "green";
              } else {
                lastseen = "--:--:--";
              }
              senC++;
              gatewayArray.push(
                "<tr id=" +
                  data[i].gatewayId +
                  "><td>" +
                  senC +
                  "</td><td>" +
                  data[i].gatewayId +
                  "</td><td>" +
                  data[i].gatewayName +
                  "</td><td><div class='circle' style='margin:auto;background-color:" +
                  status +
                  ";'></div></td><td>" +
                  lastseen +
                  "</td></tr>"
              );
            }
            // Setting the state with sensor tag list
            this.setState({ gatewaylist: [...gatewayArray] });
            this.gateway_numberOfPages = Math.ceil(
              gatewayArray.length / this.numberPerPage
            );
            this.gateway_currentRowCount = senC;

            // Calling methods to display tag list with pagination added
            this.loadGatewayList();
          } else {
            $("#sys-error").text("No gateway is turned on.");
          }
        }
      })
      .catch((error) => {
        console.log(error);
        if (error.response.status === 403) {
          $("#timeout_modal").css("display", "block");
          $("#content").text("User Session has timed out. Please Login again");
        } else {
          $("#sys-error").text(
            "Request Failed with status code (" + error.response.status + ")."
          );
        }
      });
  };

  loadGatewayList = () => {
    let begin = (this.gateway_currentPage - 1) * this.numberPerPage;
    let end = begin + this.numberPerPage;
    $("#currentpage1").text(this.gateway_currentPage);
    $("#numberofpages1").text(
      Math.ceil(this.state.gatewaylist.length / this.numberPerPage)
    );
    this.gateway_pageList = this.state.gatewaylist.slice(begin, end);
    $("#rowCount1").text(this.gateway_pageList.length);

    $("#gatewayHealth").empty();
    for (let r = 0; r < this.gateway_pageList.length; r++) {
      $("#gatewayHealth").append(this.gateway_pageList[r]);
    }
    document.getElementById("next1").disabled =
      this.gateway_currentPage === this.gateway_numberOfPages ? true : false;
    document.getElementById("previous1").disabled =
      this.gateway_currentPage === 1 ? true : false;
  };

  nextGatewayPage = () => {
    this.gateway_currentPage = this.gateway_currentPage + 1;
    this.loadGatewayList();
  };

  previousGatewayPage = () => {
    this.gateway_currentPage = this.gateway_currentPage - 1;
    this.loadGatewayList();
  };

  assetHealth = () => {
    // API call to get the all Student asset details
    axios({
      method: "GET",
      url: assetHealth,
    })
      .then((response) => {
        console.log(response);
        if (response.status === 200 || response.status === 201) {
          let data = response.data.assets;
          if (data.length !== 0) {
            let empC = 0;
            let assetArray = [];
            for (let i = 0; i < data.length; i++) {
              empC++;
              let status = "red";
              let currTime = new Date();
              let lastseen;

              if (data[i].lastseen !== null) {
                lastseen =
                  data[i].lastseen.substring(0, 10) +
                  " " +
                  data[i].lastseen.substring(11, 19);
                let timestamp = new Date(
                  lastseen.substring(0, 10) + " " + lastseen.substring(11, 19)
                );
                if (currTime - timestamp <= 2 * 60 * 1000) status = "green";
              } else {
                lastseen = "--:--:--";
              }
              assetArray.push(
                "<tr id=" +
                  data[i].tagId +
                  "><td>" +
                  empC +
                  "</td><td>" +
                  data[i].tagId +
                  "</td><td>" +
                  data[i].name +
                  "</td><td>" +
                  data[i].department +
                  "</td><td>" +
                  data[i].mailId +
                  "</td><td>" +
                  data[i].phoneNumber +
                  "</td><td>" +
                  data[i].battery +
                  "</td><td><div class='circle' style='margin:auto;background-color:" +
                  status +
                  ";'></div></td><td>" +
                  lastseen +
                  "</td></tr>"
              );
            }
            // Setting the state with tracking tag list
            this.setState({ assetlist: [...assetArray] });
            this.asset_numberOfPages = Math.ceil(
              assetArray.length / this.numberPerPage
            );
            this.asset_currentRowCount = empC;
            // Calling methods to display tag list with pagination added
            this.loadAssetList();
          } else {
            $("#sys-error").text("No asset turned on.");
          }
        }
      })
      .catch((error) => {
        console.log(error);
        if (error.response.status === 403) {
          $("#timeout_modal").css("display", "block");
          $("#content").text("User Session has timed out. Please Login again");
        } else {
          $("#sys-error").text(
            "Request Failed with status code (" + error.response.status + ")."
          );
        }
      });
  };

  loadAssetList = () => {
    let begin = (this.asset_currentPage - 1) * this.numberPerPage;
    let end = begin + this.numberPerPage;
    $("#currentpage").text(this.asset_currentPage);
    $("#numberofpages").text(
      Math.ceil(this.state.assetlist.length / this.numberPerPage)
    );
    this.asset_pageList = this.state.assetlist.slice(begin, end);
    $("#rowCount").text(this.asset_pageList.length);

    $("#assetHealth").empty();
    for (let r = 0; r < this.asset_pageList.length; r++) {
      $("#assetHealth").append(this.asset_pageList[r]);
    }
    document.getElementById("next").disabled =
      this.asset_currentPage === this.asset_numberOfPages ? true : false;
    document.getElementById("previous").disabled =
      this.asset_currentPage === 1 ? true : false;
  };

  nextAssetPage = () => {
    this.asset_currentPage = this.asset_currentPage + 1;
    this.loadAssetList();
  };

  previousAssetPage = () => {
    this.asset_currentPage = this.asset_currentPage - 1;
    this.loadAssetList();
  };

  /** Terminate the session on forbidden (403) error */
  sessionTimeout = () => {
    $("#timeout_modal").css("display", "none");
    sessionStorage.removeItem("isLoggedIn");
    this.props.handleLogin(0);
  };

  componentWillUnmount() {
    clearInterval(this.interval_getAssets);
    clearInterval(this.interval_getGateway);
  }

  render() {
    return (
      <Fragment>
        <Helmet>
          <title>System Health</title>
        </Helmet>
        <div className="panel">
          <span className="main-heading">SYSTEM HEALTH</span>
          <br />
          <img alt="" src="../images/Tiles/Underline.png" style={Underline} />
          <div className="container fading" style={{ marginTop: "40px" }}>
            <p className="error-msg" id="sys-error"></p>
            <br></br>
            <div className="container">
              {/* Maseter Gateway Table */}
              <span className="heading">Gateway Health</span>
              <br />
              <img
                src="../images/Tiles/Underline.png"
                alt=""
                style={{
                  width: "56px",
                  height: "3px",
                  marginTop: "2px",
                  position: "absolute",
                }}
              />
              <table style={{ marginTop: "20px", marginBottom: "30px" }}>
                <thead>
                  <tr>
                    <th>Sl.No</th>
                    <th>GATEWAY ID</th>
                    <th>GATEWAY NAME</th>
                    <th>STATUS</th>
                    <th>LAST SEEN</th>
                  </tr>
                </thead>
                <tbody id="gatewayHealth"></tbody>
              </table>
              {/* Page navigation options for sensor tags */}
              <div
                style={{
                  textAlign: "center",
                }}
              >
                <p style={{ float: "left", fontFamily: "Roboto-Regular" }}>
                  ( <span id="currentpage1">0</span> /
                  <span id="numberofpages1">0</span> )
                </p>
                <button
                  id="previous1"
                  onClick={this.previousGatewayPage}
                  style={{ fontFamily: "Roboto-Medium" }}
                >
                  Previous
                </button>
                <button
                  id="next1"
                  onClick={this.nextGatewayPage}
                  style={{ fontFamily: "Roboto-Medium" }}
                >
                  Next
                </button>
                <p style={{ float: "right", fontFamily: "Roboto-Regular" }}>
                  Row count : <span id="rowCount1">0</span>
                </p>
              </div>
              {/* ----------------------------------------------- */}
            </div>
            <div className="container">
              {/* Assets Table */}
              <span className="heading">Asset Health</span>
              <br />
              <img
                src="../images/Tiles/Underline.png"
                alt=""
                style={{
                  width: "56px",
                  height: "3px",
                  marginTop: "2px",
                  position: "absolute",
                }}
              />
              <table style={{ marginTop: "20px", marginBottom: "30px" }}>
                <thead>
                  <tr>
                    <th>Sl.No</th>
                    <th>MAC ID</th>
                    <th>STUDENT NAME</th>
                    <th>DEPARTMENT</th>
                    <th>MAIL ID</th>
                    <th>PHONE NUMBER</th>
                    <th>BATTERY STATUS (%)</th>
                    <th>STATUS</th>
                    <th>LAST SEEN</th>
                  </tr>
                </thead>
                <tbody id="assetHealth"></tbody>
              </table>
              {/* Page navigation options for tracking tags */}
              <div
                style={{
                  textAlign: "center",
                }}
              >
                <p style={{ float: "left", fontFamily: "Roboto-Regular" }}>
                  ( <span id="currentpage">0</span> /
                  <span id="numberofpages">0</span> )
                </p>
                <button
                  id="previous"
                  onClick={this.previousAssetPage}
                  style={{ fontFamily: "Roboto-Medium" }}
                >
                  Previous
                </button>
                <button
                  id="next"
                  onClick={this.nextAssetPage}
                  style={{ fontFamily: "Roboto-Medium" }}
                >
                  Next
                </button>
                <p style={{ float: "right", fontFamily: "Roboto-Regular" }}>
                  Row count : <span id="rowCount">0</span>
                </p>
              </div>
              {/* ----------------------------------------------- */}
            </div>
          </div>
        </div>
        {/* Display modal to display error messages */}
        <div id="timeout_modal" className="modal">
          <div className="modal-content">
            <p id="content" style={{ textAlign: "center" }}></p>
            <button
              id="ok"
              className="btn-center btn success-btn"
              onClick={this.sessionTimeout}
            >
              OK
            </button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default SystemHealth;
