import React, { Component, Fragment } from "react";
import axios from "axios";
import "./Styling.css";
import $ from "jquery";
import { studentDetails, updateAsset } from "../../urls/url";

class UpdateAsset extends Component {
  /** Hides all error and success messages displayed on all button clicks */
  hide = () => {
    $("#updateError").text("");
    $("#updateSuccess").text("");
  };

  /** Get all details of student based on student mac id
   *  return all all details if id is registered
   *  else return error
   */
  getDetails = (e) => {
    this.hide();
    e.preventDefault();
    console.log($("update_#macid").val());
    if ($("#update_macid").val().length === 0) {
      $("#updateError").text(
        "Please enter MAC ID of student to update details."
      );
    } else if (
      !$("#update_macid")
        .val()
        .match("([A-Za-z0-9]{2}[-]){5}([A-Za-z0-9]){2}") &&
      $("#update_macid").val().length !== 17
    ) {
      $("#updateError").text("Invalid MAC ID entered.");
    } else {
      axios({
        method: "POST",
        url: studentDetails,
        data: { tagId: $("#update_macid").val() },
      })
        .then((response) => {
          console.log(response.data);
          if (response.status === 200 || response.status === 201) {
            if (response.data.tagId.length !== 0) {
              $("#sudent_details").css("display", "none");
              $("#update-form").css("display", "block");
              console.log(response.data.tagId);
              $("#update_tagid").val(response.data.tagId);
              $("#update_studentName").val(response.data.studentName);
              $("#update_regNo").val(response.data.registrationNo);
              $("#update_standard").val(response.data.standard);
              $("#update_section").val(response.data.section);
              $("#update_gender").val(response.data.gender);
            }
          }
        })
        .catch((error) => {
          console.log(error);
          if (error.response.status === 403) {
            $("#timeout_modal2").css("display", "block");
            $("#content").text(
              "User Session has timed out. Please Login again"
            );
          } else {
            $("#updateError").text(
              "Request Failed with status code (" + error.response.status + ")."
            );
          }
        });
    }
  };
  /** Student Updation method with following details
   *  Student Name, Registration Number, Standard studying in, Section, Gender and Tag MAC address.
   *  All the fields are mandatory for registration
   */
  update = (e) => {
    this.hide();
    e.preventDefault();
    $("#delete-form").css("display", "none");
    let data = {
      tagId: $("#update_tagid").val(),
      studentName: $("#update_studentName").val(),
      registrationNo: $("#update_regNo").val(),
      department: $("#update_department").val(),
      mailId: $("#update_mailid").val(),
      phoneNumber: $("#update_phoneno").val(),
      gender: $("#update_gender").val(),
    };
    if (
      data.tagId === "" ||
      data.studentName === "" ||
      data.registrationNo === "" ||
      data.department === "" ||
      data.mailId === "" ||
      data.phoneNumber === "" ||
      data.gender === ""
    )
      $("#updateError").text("Please enter all the fields.");
    else if (
      !data.tagId.match("([A-Za-z0-9]{2}[-]){5}([A-Za-z0-9]){2}") &&
      data.tagId.length !== 17
    )
      $("#updateError").text("Invalid MAC ID entered.");
    else {
      $("#updateError").text("");
      console.log(data);
      /* POST API call to register student with all given details */
      axios({
        method: "PUT",
        url: updateAsset,
        data: data,
      })
        .then((response) => {
          console.log(response);
          if (response.status === 200 || response.status === 201) {
            $("#updateSuccess").text("Student is registered successfully.");
          } else {
            $("#updateError").text("Unable to Register Student.");
          }
        })
        .catch((error) => {
          console.log(error);
          if (error.response.status === 403) {
            $("#timeout_modal2").css("display", "block");
            $("#content").text(
              "User Session has timed out. Please Login again"
            );
          } else {
            $("#updateError").text(
              "Request Failed with status code (" + error.response.status + ")."
            );
          }
        });
    }
    $("input[type=text]").val("");
  };

  /** Terminate the session on forbidden (403) error */
  sessionTimeout = () => {
    $("#timeout_modal2").css("display", "none");
    sessionStorage.removeItem("isLoggedIn");
    this.props.handleLoginError();
  };

  render() {
    return (
      <Fragment>
        <span className="sub-heading">Update Student</span>
        <br />
        <img
          src="../images/Tiles/Underline.png"
          alt=""
          style={{
            width: "56px",
            height: "3px",
            marginTop: "2px",
            position: "absolute",
          }}
        />
        <br></br>
        <div>
          {/* Element for displaying error messages */}
          <span className="error-msg" id="updateError"></span>
          <span className="success-msg" id="updateSuccess"></span>
        </div>
        {/* ------------Form to get already registered student details based tag mac id specified-------------- */}
        <form id="sudent_details">
          {/* Student tag mac id input field */}
          <div className="input-group">
            <span className="label">Tag MAC ID :</span>
            <input
              type="text"
              id="update_macid"
              required="required"
              onClick={this.hide}
              placeholder="5a-c2-15-00-00-00"
            />
          </div>
          <div className="input-group">
            <input
              type="submit"
              value="Get Student Details"
              onClick={this.getDetails}
              className="btn success-btn"
            />
          </div>
        </form>
        {/*-------------- Student Updation form bases on registered mac id---------------- */}
        <form id="update-form" style={{ display: "none" }}>
          {/* Input field for Tag MAC ID */}
          <div className="input-group">
            <span className="label">Tag MAC ID :</span>
            <input
              type="text"
              id="update_tagid"
              onClick={this.hide}
              disabled="disabled"
            />
          </div>
          {/* Input field for Student Name */}
          <div className="input-group">
            <span className="label">Student Name :</span>
            <input type="text" id="update_studentName" required="required" />
          </div>
          {/* Input field for Student Register Number */}
          <div className="input-group">
            <span className="label">Registration Number :</span>
            <input type="text" id="update_regNo" required="required" />
          </div>
          {/* Department input field */}
          <div className="input-group">
            <span className="label">Department :</span>
            <input type="text" id="update_department" required="required" />
          </div>
          {/* Mail id input field */}
          <div className="input-group">
            <span className="label">Mail ID :</span>
            <input type="email" id="update_mailid" required="required" />
          </div>
          {/* Phonenumber input field */}
          <div className="input-group">
            <span className="label">Phone Number :</span>
            <input type="tel" id="update_phoneno" required="required" />
          </div>
          {/* Input field for Email ID */}
          <div className="input-group">
            <span className="label">Gender :</span>
            <select id="update_gender" required="required">
              <option value="Male">Male</option>
              <option value="Female">Female</option>
              <option value="Other">Other</option>
            </select>
          </div>
          <div className="input-group">
            <input
              type="submit"
              value="Update"
              onClick={this.update}
              className="btn success-btn"
            />
          </div>
        </form>
        {/* Display modal to display error messages */}
        <div id="timeout_modal2" className="modal">
          <div className="modal-content">
            <p id="content" style={{ textAlign: "center" }}></p>
            <button
              id="ok"
              className="btn-center btn success-btn"
              onClick={this.sessionTimeout}
            >
              OK
            </button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default UpdateAsset;
