import React from "react";
import { Link } from "react-router-dom";
import "./Navbar.css";

import {
  home,
  configuration,
  contactTracing,
  dailyReport,
  assets,
  systemHealth,
  attendanceSheet,
} from "../../urls/route";

// Internal properties for changing the margin and background color of images on side navigation bar
const Margin = { margin: "40px 0px" };
const Background = { backgroundColor: "rgba(2,167,140,0.2)" };

/** linkClicked takes id of the image that is clicked on the side navigation bar
 *  and changes the background color of only that image
 */
export function linkClicked(id) {
  let element = document.getElementsByClassName("linkImg");
  element[0].style.backgroundColor = "";
  element[1].style.backgroundColor = "";
  element[2].style.backgroundColor = "";
  element[3].style.backgroundColor = "";
  element[4].style.backgroundColor = "";
  element[5].style.backgroundColor = "";
  element[6].style.backgroundColor = "";
  element[id].style.backgroundColor = "rgba(2,167,140,0.2)";
}

/*  Navbar is a functional component to display the side navigation bar with navigation options
    in the form of images */
function Navbar() {
  return (
    <div className="container">
      <div className="sidebar">
        {/* Link for navigation to Home Component */}
        <Link to={home}>
          <div style={Margin}>
            <img
              alt=""
              src="../images/Icons/Icon_Home.png"
              title="Home"
              onClick={() => linkClicked(0)}
              className={"linkImg"}
              style={Background}
            />
          </div>
        </Link>
        {/* Link for navigation to Configuration Component */}
        <Link to={configuration}>
          <div>
            <img
              alt=""
              src="../images/Icons/Icon_Config.png"
              title="Configuration"
              onClick={() => linkClicked(1)}
              className={"linkImg"}
            />
          </div>
        </Link>
        {/* Link for navigation to All Assets Component */}
        <Link to={assets}>
          <div>
            <img
              alt=""
              src="../images/Icons/Icon_Allassets.png"
              title="All Assets"
              onClick={() => linkClicked(2)}
              className={"linkImg"}
            />
          </div>
        </Link>
        {/* Link for navigation to Contact Tracing Component */}
        <Link to={contactTracing}>
          <div>
            <img
              alt=""
              src="../images/Icons/Icon_Tracing.png"
              title="Contact Tracing"
              onClick={() => linkClicked(3)}
              className={"linkImg"}
            />
          </div>
        </Link>
        {/* Link for navigation to System Health Component */}
        <Link to={systemHealth}>
          <div>
            <img
              alt=""
              src="../images/Icons/Icon_Systemhealth.png"
              title="System Health"
              onClick={() => linkClicked(4)}
              className={"linkImg"}
            />
          </div>
        </Link>
        {/* Link for navigation to Attendance Sheet Component */}
        <Link to={attendanceSheet}>
          <div>
            <img
              alt=""
              src="../images/Icons/Icon_Attendance.png"
              title="Attendance Sheet"
              onClick={() => linkClicked(5)}
              className={"linkImg"}
            />
          </div>
        </Link>
        {/* Link for navigation to Attendance Sheet Component */}
        <Link to={dailyReport}>
          <div>
            <img
              alt=""
              src="../images/Icons/Icon_Reports.png"
              title="Daily Report"
              onClick={() => linkClicked(6)}
              className={"linkImg"}
            />
          </div>
        </Link>
      </div>
    </div>
  );
}

export default Navbar;
