import React, { Fragment, Component } from "react";
import $ from "jquery";
import * as XLSX from "xlsx";
import axios from "axios";
import { bulkUpload } from "../../urls/url";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

class BulkUpload extends Component {
  constructor() {
    super();
    this.state = { file: "", studentsData: [] };
  }

  handleClick = (e) => {
    console.log(e.target.files[0]);
    this.setState({ file: e.target.files[0] });
  };

  readFile = () => {
    if (this.state.file === "") {
      $("#uploadError").text("Please select a file to upload.");
    } else {
      // console.log(this.state.file);
      var f = this.state.file;
      // var name = f.name;
      const reader = new FileReader();
      reader.onload = (evt) => {
        // evt = on_file_select event
        /* Parse data */
        const bstr = evt.target.result;
        const wb = XLSX.read(bstr, { type: "binary" });
        /* Get first worksheet */
        const wsname = wb.SheetNames[0];
        const ws = wb.Sheets[wsname];
        /* Convert array of arrays */
        const data = XLSX.utils.sheet_to_csv(ws, { header: 1 });
        /* Update state */
        var result = this.convertToJson(data);
        if (result) {
          console.log(this.state.studentsData);
          axios({
            method: "POST",
            url: bulkUpload,
            data: this.state.studentsData,
          })
            .then((response) => {
              console.log(response);
              if (response.status === 200 || response.status === 201) {
                $("#uploadSuccess").text("Bulk upload is succesful.");
              } else {
                $("#uploadError").text("Bulk upload failed.");
              }
            })
            .catch((error) => {
              console.log(error);
              if (error.response.status === 403) {
                $("#bulk_timeout_modal1").css("display", "block");
                $("#content").text(
                  "User Session has timed out. Please Login again"
                );
              } else {
                $("#uploadError").text(
                  "Request Failed with status code (" +
                    error.response.status +
                    ")."
                );
              }
            });
        }
      };
      reader.readAsBinaryString(f);
    }
  };

  convertToJson(csv) {
    var lines = csv.split("\n");
    var result = [];
    console.log(csv);
    // var headers = lines[0].split(",");
    var headers = [
      "tagId",
      "studentName",
      "registrationNo",
      "department",
      "mailId",
      "phoneNumber",
      "gender",
    ];
    for (var i = 1; i < lines.length - 1; i++) {
      console.log(i);
      var obj = {};
      var currentline = lines[i].split(",");
      //Validating TAG MAC ID
      if (
        currentline[0].length !== 17
        // !currentline[0].match("([A-Za-z0-9]{2}[-]){5}([A-Za-z0-9]){2}")
      ) {
        $("#uploadError").text("Invalid TAG ID at line no." + (i + 1));
        break;
      } else {
        obj[headers[0]] = currentline[0];
        // Validating student name
        if (currentline[1] === "") {
          $("#uploadError").text(
            "Please check line no. " + (i + 1) + " some field(s) are missing."
          );
          break;
        } else {
          obj[headers[1]] = currentline[1];
          // Validating registration number
          if (currentline[2] === "") {
            $("#uploadError").text(
              "Please check line no. " + (i + 1) + " some field(s) are missing."
            );

            break;
          } else {
            obj[headers[2]] = currentline[2];
            // Validating department
            if (currentline[3] === "") {
              $("#uploadError").text(
                "Please check line no. " +
                  (i + 1) +
                  " some field(s) are missing."
              );

              break;
            } else {
              obj[headers[3]] = currentline[3];
              // Validating mailid
              if (currentline[4] === "") {
                $("#uploadError").text(
                  "Please check line no. " +
                    (i + 1) +
                    " some field(s) are missing."
                );

                break;
              } else {
                obj[headers[4]] = currentline[4];
                // Validating phone number
                if (currentline[5] === "") {
                  $("#uploadError").text(
                    "Please check line no. " +
                      (i + 1) +
                      " some field(s) are missing."
                  );

                  break;
                } else {
                  obj[headers[5]] = currentline[5];
                  // Validating gender
                  if (currentline[6] === "") {
                    $("#uploadError").text(
                      "Please check line no. " +
                        (i + 1) +
                        " some field(s) are missing."
                    );

                    break;
                  } else {
                    obj[headers[6]] = currentline[6];
                    result.push(obj);
                  }
                }
              }
            }
          }
        }
      }
    }
    if (result.length === lines.length - 2) {
      this.setState({ studentsData: result });
      return true;
    } else {
      return false;
    }
  }

  /** Terminate the session on forbidden (403) error */
  sessionTimeout = () => {
    $("#bulk_timeout_modal1").css("display", "none");
    sessionStorage.removeItem("isLoggedIn");
    this.props.handleLoginError();
  };

  render() {
    return (
      <Fragment>
        <div>
          {/* Element for displaying error messages */}
          <p className="error-msg" id="uploadError"></p>
          <p className="success-msg" id="uploadSuccess"></p>
        </div>
        <div className="row colorBlock">
          {/* Bulk uploading of Tracking Tags */}
          <div className="input-group">
            <span className="sub-heading">Student Data Sheet : </span>
            <input
              type="file"
              id="fileUpload"
              name="fileUpload"
              accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
              onChange={this.handleClick}
            />
            <input
              type="button"
              id="upload"
              className="btn success-btn"
              value="Upload File"
              onClick={this.readFile}
              style={{ height: "40px", marginLeft: "10px" }}
            />
          </div>
          <div style={{ marginTop: "10px", float: "left" }}>
            <img
              alt=""
              src="../images/Icons/Icon_Download.png"
              style={{
                width: "15px",
                height: "15px",
                marginRight: "10px",
              }}
            />
            <ReactHTMLTableToExcel
              // id="test-table-xls-button"
              className="downloadlink"
              table="studentDetails"
              filename="StudentDetails"
              sheet="StudentDetails"
              buttonText="Sample Excel Sheet for Bulk Registration"
            />
          </div>
          <table id="studentDetails" style={{ display: "none" }}>
            <tr>
              <th>TAG MAC ID</th>
              <th>STUDENT NAME</th>
              <th>REGISTRATION NO</th>
              <th>DEPARTMENT</th>
              <th>MAILID</th>
              <th>PHONE NUMBER</th>
              <th>GENDER</th>
            </tr>
          </table>
        </div>

        {/* Display modal to display error messages */}
        <div id="bulk_timeout_modal1" className="modal">
          <div className="modal-content">
            <p id="content" style={{ textAlign: "center" }}></p>
            <button
              id="ok"
              className="btn-center btn success-btn"
              onClick={this.sessionTimeout}
            >
              OK
            </button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default BulkUpload;
