import React, { Component } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
// all component import statements
import Login from "./components/login/Login";
import Header from "./components/header/Header";
import Navbar from "./components/side_navigationbar/Navbar";
import Home from "./components/all_pages/Home";
import Configuration from "./components/all_pages/Configuration";
import Tracing from "./components/all_pages/Tracing";
import Assets from "./components/all_pages/Assets";
import SystemHealth from "./components/all_pages/SystemHealth";
import DailyReport from "./components/all_pages/DailyReport";
import AttendanceSheet from "./components/all_pages/AttendanceSheet";
import {
  rootUrl,
  home,
  configuration,
  contactTracing,
  login,
  dailyReport,
  attendanceSheet,
  assets,
  systemHealth,
} from "./urls/route";

class App extends Component {
  // Defining the states for the component
  constructor() {
    super();
    this.state = {
      isLoggedIn: parseInt(0),
    };
  }

  /** Method to change state bases on login status */
  handleUserLogin = (credentials) => {
    this.setState({ isLoggedIn: parseInt(credentials) });
  };

  /** Redern the html content on the browser on component load*/
  render() {
    // Render home component on state set to true
    if (parseInt(sessionStorage.getItem("isLoggedIn")) === 1) {
      return (
        <Router>
          <Header handleLogin={this.handleUserLogin}></Header>
          <Navbar></Navbar>
          <Switch>
            <Route exact path={rootUrl}>
              <Redirect to={home}></Redirect>
            </Route>
            <Route exact path={login}>
              <Redirect to={home}></Redirect>
            </Route>
            <Route
              exact
              path={home}
              render={(props) => (
                <Home {...props} handleLogin={this.handleUserLogin}></Home>
              )}
            />
            <Route
              exact
              path={configuration}
              render={(props) => (
                <Configuration
                  {...props}
                  handleLogin={this.handleUserLogin}
                ></Configuration>
              )}
            />
            <Route
              exact
              path={contactTracing}
              render={(props) => (
                <Tracing
                  {...props}
                  handleLogin={this.handleUserLogin}
                ></Tracing>
              )}
            />
            <Route
              exact
              path={dailyReport}
              render={(props) => (
                <DailyReport
                  {...props}
                  handleLogin={this.handleUserLogin}
                ></DailyReport>
              )}
            />
            <Route
              exact
              path={attendanceSheet}
              render={(props) => (
                <AttendanceSheet
                  {...props}
                  handleLogin={this.handleUserLogin}
                ></AttendanceSheet>
              )}
            />
            <Route
              exact
              path={assets}
              render={(props) => (
                <Assets {...props} handleLogin={this.handleUserLogin}></Assets>
              )}
            />
            <Route
              exact
              path={systemHealth}
              render={(props) => (
                <SystemHealth
                  {...props}
                  handleLogin={this.handleUserLogin}
                ></SystemHealth>
              )}
            />
          </Switch>
        </Router>
      );
    } else {
      return (
        <Router>
          <Route path="/">
            <Redirect to="/login"></Redirect>
          </Route>
          <Route
            exact
            path="/login"
            render={(props) => (
              <Login {...props} handleLogin={this.handleUserLogin}></Login>
            )}
          />
        </Router>
      );
    }
  }
}

export default App;
