import React, { Component, Fragment } from "react";
import { Helmet } from "react-helmet";
import { linkClicked } from "../side_navigationbar/Navbar";
import axios from "axios";
import $ from "jquery";
import "./Styling.css";
import { assetDetails, studentDetails } from "../../urls/url";

axios.defaults.xsrfHeaderName = "x-csrftoken";
axios.defaults.xsrfCookieName = "csrftoken";

// Styling property for Underline Image added for the Page heading
const Underline = {
  width: "75px",
  height: "9px",
  position: "absolute",
};

// Class component defined here
class Assets extends Component {
  numberPerPage = 15;

  // variables for gateway table used for pagination
  asset_pageList = [];
  asset_currentPage = 1;
  asset_numberOfPages = 0;
  asset_currentRowCount = 0;

  /** Defining the states of the Component */
  constructor() {
    super();
    this.state = {
      assetlist: [],
    };
  }

  /** Method is called on Component Load */
  componentDidMount() {
    linkClicked(2);
    this.studentInformation();
  }

  studentInformation = () => {
    // API call to get the all Student asset details
    $("#asset-error").text("");
    $("#rowCount").text("0");
    axios({
      method: "GET",
      url: assetDetails,
    })
      .then((response) => {
        console.log(response);
        if (response.status === 200 || response.status === 201) {
          let data = response.data.assets;
          if (data.length > 0) {
            let studentCount = 0;
            let assetArray = [];
            for (let i = 0; i < data.length; i++) {
              studentCount++;
              assetArray.push(
                "<tr id=" +
                  data[i].tagId +
                  "><td>" +
                  studentCount +
                  "</td><td>" +
                  data[i].tagId +
                  "</td><td>" +
                  data[i].studentName +
                  "</td><td>" +
                  data[i].registrationNo +
                  "</td><td>" +
                  data[i].department +
                  "</td><td>" +
                  data[i].mailId +
                  "</td><td>" +
                  data[i].phoneNumber +
                  "</td><td>" +
                  data[i].gender +
                  "</td></tr>"
              );
            }
            // Setting the state with tracking tag list
            this.setState({ assetlist: [...assetArray] });
            this.asset_numberOfPages = Math.ceil(
              assetArray.length / this.numberPerPage
            );
            this.asset_currentRowCount = studentCount;
            // Calling methods to display tag list with pagination added
            this.loadAssetList();
          } else {
            $("#assetTable").empty();
            $("#asset-error").text("No data to display.");
          }
        }
      })
      .catch((error) => {
        console.log(error);
        if (error.response.status === 403) {
          $("#timeout_modal").css("display", "block");
          $("#content").text("User Session has timed out. Please Login again");
        } else {
          $("#asset-error").text(
            "Request Failed with status code (" + error.response.status + ")."
          );
        }
      });
  };

  loadAssetList = () => {
    let begin = (this.asset_currentPage - 1) * this.numberPerPage;
    let end = begin + this.numberPerPage;
    $("#currentpage").text(this.asset_currentPage);
    $("#numberofpages").text(
      Math.ceil(this.state.assetlist.length / this.numberPerPage)
    );
    this.asset_pageList = this.state.assetlist.slice(begin, end);
    $("#rowCount").text(this.asset_pageList.length);

    $("#assetTable").empty();
    for (let r = 0; r < this.asset_pageList.length; r++) {
      $("#assetTable").append(this.asset_pageList[r]);
    }
    document.getElementById("next").disabled =
      this.asset_currentPage === this.asset_numberOfPages ? true : false;
    document.getElementById("previous").disabled =
      this.asset_currentPage === 1 ? true : false;
  };

  nextAssetPage = () => {
    this.asset_currentPage = this.asset_currentPage + 1;
    this.loadAssetList();
  };

  previousAssetPage = () => {
    this.asset_currentPage = this.asset_currentPage - 1;
    this.loadAssetList();
  };

  search = () => {
    // Fetcing data entered by user for searching data
    let tagid = $("#tagid").val();
    $("#asset-error").text("");

    // Conditional statements to get the data and display in table
    // Displys all tracking tags
    if (tagid === "") {
      $("#asset-error").text("Please enter Tag ID.");
    } else if (
      tagid.length !== 17 &&
      !tagid.match("([A-Za-z0-9]{2}[-]){5}([A-Za-z0-9]){2}")
    ) {
      $("#asset-error").text("Invalid Tag ID entered.");
    } else {
      // Erasing already existing table content
      $("#tempTable").empty();
      $("#detailsTable").css("display", "none");
      $("#tempTable").css("display", "table");
      axios({
        method: "POST",
        url: studentDetails,
        data: { tagId: tagid },
      })
        .then((response) => {
          console.log(response);
          if (response.status === 200 || response.status === 201) {
            if (response.data.tagId.length !== 0) {
              $("#tempTable").append(
                "<tr><th>TAG ID</th><th>STUDENT NAME</th><th>REG. NO.</th><th>DEPARTMENT</th><th>MAIL ID</th><th>PHONE NUMBER</th><th>GENDER</th></tr>"
              );
              $("#tempTable").append(
                "<tr><td>" +
                  response.data.tagId +
                  "</td><td>" +
                  response.data.studentName +
                  "</td><td>" +
                  response.data.registrationNo +
                  "</td><td>" +
                  response.data.department +
                  "</td><td>" +
                  response.data.mailId +
                  "</td><td>" +
                  response.data.phoneNumber +
                  "</td><td>" +
                  response.data.gender +
                  "</td></tr>"
              );
            } else {
              $("#asset-error").text("No data found.");
            }
          }
        })
        .catch((error) => {
          console.log(error);
          if (error.response.status === 403) {
            $("#timeout_modal2").css("display", "block");
            $("#content").text(
              "User Session has timed out. Please Login again"
            );
          } else {
            $("#asset-error").text(
              "Request Failed with status code (" + error.response.status + ")."
            );
          }
        });
    }
  };

  /** Terminate the session on forbidden (403) error */
  sessionTimeout = () => {
    $("#timeout_modal").css("display", "none");
    sessionStorage.removeItem("isLoggedIn");
    this.props.handleLogin(0);
  };

  /** Redern the html content on the browser */
  render() {
    return (
      <Fragment>
        <Helmet>
          <title>Assets</title>
        </Helmet>
        <div className="panel">
          <span className="main-heading">STUDENT DETAILS</span>
          <br />
          <img alt="" src="../images/Tiles/Underline.png" style={Underline} />

          <div className="container fading" style={{ marginTop: "50px" }}>
            {/* Tag search options for both tracking and sensor tag */}
            <div className="row">
              {/* Input field for tag MAC ID */}
              <div className="input-group">
                <span className="label">Tag MAC ID : </span>
                <input
                  type="text"
                  id="tagid"
                  placeholder="5a-c2-15-00-00-00"
                  required="required"
                />
              </div>
              {/* Button for searching tag */}
              <div className="input-group">
                <input
                  type="button"
                  value="Search"
                  onClick={this.search}
                  className="btn success-btn"
                />
                &nbsp;&nbsp;
                {/* Clearing search data and hiding search result table */}
                <input
                  type="button"
                  value="Clear"
                  onClick={() => {
                    $("#tempTable").css("display", "none");
                    $("#detailsTable").css("display", "block");
                    $("#tagid").val("");
                    $("#asset-error").text("");
                  }}
                  className="btn success-btn"
                />
              </div>
            </div>
            <hr />
            <p className="error-msg" id="asset-error"></p>
            {/* Table for displaying search result */}
            <table
              id="tempTable"
              style={{
                marginBottom: "30px",
              }}
            ></table>
            {/* TRACKING TAGS TABLE */}
            {/* Table to display all student details for particular standard */}
            <div id="detailsTable">
              <table
                style={{
                  marginBottom: "30px",
                }}
              >
                <thead>
                  <tr>
                    <th>Sl.No</th>
                    <th>TAG MAC ID</th>
                    <th>STUDENT NAME</th>
                    <th>REGISTER NO.</th>
                    <th>DEPARTMENT</th>
                    <th>MAIL ID</th>
                    <th>PHONE NUMBER</th>
                    <th>GENDER</th>
                  </tr>
                </thead>
                <tbody id="assetTable"></tbody>
              </table>
              {/* Page navigation options for tracking tags */}
              <div
                style={{
                  textAlign: "center",
                }}
              >
                <p style={{ float: "left", fontFamily: "Roboto-Regular" }}>
                  ( <span id="currentpage">0</span> /
                  <span id="numberofpages">0</span> )
                </p>
                <button
                  id="previous"
                  onClick={this.previousAssetPage}
                  style={{ fontFamily: "Roboto-Medium" }}
                >
                  Previous
                </button>
                <button
                  id="next"
                  onClick={this.nextAssetPage}
                  style={{ fontFamily: "Roboto-Medium" }}
                >
                  Next
                </button>
                <p style={{ float: "right", fontFamily: "Roboto-Regular" }}>
                  Row count : <span id="rowCount">0</span>
                </p>
              </div>
            </div>
            {/* ----------------------------------------------------- */}
          </div>
        </div>
        {/* Display modal to display error messages */}
        <div id="timeout_modal" className="modal">
          <div className="modal-content">
            <p id="content" style={{ textAlign: "center" }}></p>
            <button
              id="ok"
              className="btn-center btn success-btn"
              onClick={this.sessionTimeout}
            >
              OK
            </button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default Assets;
