import React, { Component, Fragment } from "react";
import axios from "axios";
import "./Styling.css";
import $ from "jquery";
import { studentRegistrationAPI } from "../../urls/url";

class RegAsset extends Component {
  componentDidMount() {}

  /** Hides all error and success messages displayed on all button clicks */
  hide = () => {
    $("#regError").text("");
    $("#regSuccess").text("");
  };

  /** Student Registration method with following details
   *  Student Name, Registration Number, Standard studying in, Section, Gender and Tag MAC address.
   *  All the fields are mandatory for registration
   */
  register = (e) => {
    this.hide();
    e.preventDefault();
    $("#delete-form").css("display", "none");
    let data = {
      tagId: $("#tagid").val(),
      studentName: $("#studentName").val(),
      registrationNo: $("#regNo").val(),
      department: $("#department").val(),
      mailId: $("#mailid").val(),
      phoneNumber: $("#phoneno").val(),
      gender: $("#gender").val(),
    };
    if (
      data.tagId === "" ||
      data.studentName === "" ||
      data.registrationNo === "" ||
      data.department === "" ||
      data.mailId === "" ||
      data.phoneNumber === "" ||
      data.gender === ""
    )
      $("#regError").text("Please enter all the fields.");
    else if (
      !data.tagId.match("([A-Za-z0-9]{2}[-]){5}([A-Za-z0-9]){2}") &&
      data.tagId.length !== 17
    )
      $("#regError").text("Invalid MAC ID entered.");
    else {
      $("#regError").text("");
      console.log(data);
      /* POST API call to register student with all given details */
      axios({
        method: "POST",
        url: studentRegistrationAPI,
        data: data,
      })
        .then((response) => {
          console.log(response);
          if (response.status === 200 || response.status === 201) {
            $("#regSuccess").text("Student data is registered successfully.");
          } else {
            $("#regError").text("Unable to Register Student.");
          }
        })
        .catch((error) => {
          console.log(error);
          if (error.response.status === 403) {
            $("#timeout_modal1").css("display", "block");
            $("#content").text(
              "User Session has timed out. Please Login again"
            );
          } else if (error.response.status === 400) {
            $("#regError").text("Tag is already registered.");
          } else {
            $("#regError").text(
              "Request Failed with status code (" + error.response.status + ")."
            );
          }
        });
    }
    $("input[type=text]").val("");
  };

  /** Terminate the session on forbidden (403) error */
  sessionTimeout = () => {
    $("#timeout_modal1").css("display", "none");
    sessionStorage.removeItem("isLoggedIn");
    this.props.handleLoginError();
  };

  render() {
    return (
      <Fragment>
        <span className="sub-heading">Register Student</span>
        <br />
        <img
          src="../images/Tiles/Underline.png"
          alt=""
          style={{
            width: "56px",
            height: "3px",
            marginTop: "2px",
            position: "absolute",
          }}
        />
        <br></br>
        <div>
          {/* Element for displaying error messages */}
          <span className="error-msg" id="regError"></span>
          <span className="success-msg" id="regSuccess"></span>
        </div>
        {/* --------------Student Registration form with following fields------------ */}
        <form id="reg-form">
          {/* MAC ID of the Tag assigned to student */}
          <div className="input-group">
            <span className="label">Tag MAC ID :</span>
            <input
              type="text"
              id="tagid"
              required="required"
              onClick={this.hide}
              placeholder="5a-c2-15-00-00-00"
            />
          </div>
          {/* Student name input field */}
          <div className="input-group">
            <span className="label">Student Name :</span>
            <input type="text" id="studentName" required="required" />
          </div>
          {/*  Student Register Number Input field */}
          <div className="input-group">
            <span className="label">Registration Number :</span>
            <input type="text" id="regNo" required="required" />
          </div>
          {/* Department input field */}
          <div className="input-group">
            <span className="label">Department :</span>
            <input type="text" id="department" required="required" />
          </div>
          {/* Mail id input field */}
          <div className="input-group">
            <span className="label">Mail ID :</span>
            <input type="email" id="mailid" required="required" />
          </div>
          {/* Phonenumber input field */}
          <div className="input-group">
            <span className="label">Phone Number :</span>
            <input type="tel" id="phoneno" required="required" />
          </div>
          {/* Gender Input field */}
          <div className="input-group">
            <span className="label">Gender :</span>
            <select id="gender" required="required">
              <option value="Male">Male</option>
              <option value="Female">Female</option>
              <option value="Other">Other</option>
            </select>
          </div>
          <div className="input-group">
            <input
              type="submit"
              value="Register"
              onClick={this.register}
              className="btn success-btn"
            />
          </div>
        </form>

        {/* Display modal to display error messages */}
        <div id="timeout_modal1" className="modal">
          <div className="modal-content">
            <p id="content" style={{ textAlign: "center" }}></p>
            <button
              id="ok"
              className="btn-center btn success-btn"
              onClick={this.sessionTimeout}
            >
              OK
            </button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default RegAsset;
