import React, { Component, Fragment } from "react";
import { Helmet } from "react-helmet";
import { linkClicked } from "../side_navigationbar/Navbar";
import axios from "axios";
import $ from "jquery";
import "./Styling.css";
import { dailyReport } from "../../urls/url";

axios.defaults.xsrfHeaderName = "x-csrftoken";
axios.defaults.xsrfCookieName = "csrftoken";

// Styling property for Underline Image
const Underline = {
  width: "75px",
  height: "9px",
  position: "absolute",
};

class DailyReport extends Component {
  numberPerPage = 20;

  // variables for gateway table used for pagination
  pageList = [];
  currentPage = 1;
  numberOfPages = 0;
  currentRowCount = 0;

  /** Defining the states of the Component */
  constructor() {
    super();
    this.state = {
      reportlist: [],
    };
  }

  /** Method is called on Component Load */
  componentDidMount() {
    linkClicked(6);
    this.getTodayReport();
    this.interval_report = setInterval(this.getTodayReport, 15 * 1000);
  }

  getTodayReport = () => {
    $("#dailyReportTable").empty();
    $("#attendance-error").text("");
    axios({ method: "GET", url: dailyReport })
      .then((response) => {
        console.log(response);
        if (response.status === 200 || response.status === 201) {
          let data = response.data.attendance;
          if (data.length !== 0) {
            let count = 0;
            let report = [];
            for (let i = 0; i < data.length; i++) {
              count++;
              report.push(
                "<tr id=" +
                  data[i].tagId +
                  "><td>" +
                  count +
                  "</td><td>" +
                  data[i].name +
                  "</td><td>" +
                  data[i].department +
                  "</td><td>" +
                  data[i].mailId +
                  "</td><td>" +
                  data[i].phoneNumber +
                  "</td><td>" +
                  data[i].intime.substring(0, 10) +
                  " " +
                  data[i].intime.substring(11, 19) +
                  "</td><td>" +
                  data[i].lastseen.substring(0, 10) +
                  " " +
                  data[i].lastseen.substring(11, 19) +
                  "</td></tr>"
              );
            }
            // Setting the state with tracking tag list
            this.setState({ reportlist: [...report] });
            this.numberOfPages = Math.ceil(report.length / this.numberPerPage);
            this.currentRowCount = count;
            // Calling methods to display tag list with pagination added
            this.loadList();
          } else {
            $("#rowCount").text("0");
            $("#attendance-error").text("No data found for search option.");
          }
        }
      })
      .catch((error) => {
        console.log(error);
        if (error.response.status === 403) {
          $("#timeout_modal").css("display", "block");
          $("#content").text("User Session has timed out. Please Login again");
        } else if (error.response.status === 500) {
          $("#attendance-error").text("No data to display");
        } else {
          $("#attendance-error").text(
            "Request Failed with status code (" + error.response.status + ")."
          );
        }
      });
  };

  loadList = () => {
    let begin = (this.currentPage - 1) * this.numberPerPage;
    let end = begin + this.numberPerPage;
    $("#currentpage").text(this.currentPage);
    $("#numberofpages").text(
      Math.ceil(this.state.reportlist.length / this.numberPerPage)
    );
    this.pageList = this.state.reportlist.slice(begin, end);
    $("#rowCount").text(this.pageList.length);

    $("#dailyReportTable").empty();
    for (let r = 0; r < this.pageList.length; r++) {
      $("#dailyReportTable").append(this.pageList[r]);
    }
    document.getElementById("next").disabled =
      this.currentPage === this.numberOfPages ? true : false;
    document.getElementById("previous").disabled =
      this.currentPage === 1 ? true : false;
  };

  nextPage = () => {
    this.currentPage = this.currentPage + 1;
    this.loadList();
  };

  previousPage = () => {
    this.currentPage = this.currentPage - 1;
    this.loadList();
  };

  /** Terminate the session on forbidden (403) error */
  sessionTimeout = () => {
    $("#timeout_modal").css("display", "none");
    sessionStorage.removeItem("isLoggedIn");
    this.props.handleLogin(0);
  };

  componentWillUnmount = () => {
    clearInterval(this.interval_report);
  };

  /** Redern the html content on the browser */
  render() {
    return (
      <Fragment>
        <Helmet>
          <title>Attendance</title>
        </Helmet>
        <div className="panel">
          <span className="main-heading">Daily Report</span>
          <br />
          <img alt="" src="../images/Tiles/Underline.png" style={Underline} />

          <div className="container fading" style={{ marginTop: "50px" }}>
            <p className="error-msg" id="attendance-error"></p>

            <div className="row" id="tables">
              {/* TRACKING TAGS TABLE */}
              <div className="container">
                {/* Table displays Student tags registered */}
                <table
                  style={{
                    marginTop: "20px",
                    marginBottom: "30px",
                  }}
                >
                  <thead>
                    <tr>
                      <th>Sl .No</th>
                      <th>STUDENT NAME</th>
                      <th>DEPARTMENT</th>
                      <th>MAIL ID</th>
                      <th>PHONE NUMBER</th>
                      <th>INTIME</th>
                      <th>LAST SEEN</th>
                    </tr>
                  </thead>
                  <tbody id="dailyReportTable"></tbody>
                </table>
                {/* Page navigation options for sensor tags */}
                <div
                  style={{
                    textAlign: "center",
                  }}
                >
                  <p style={{ float: "left", fontFamily: "Roboto-Regular" }}>
                    ( <span id="currentpage">0</span> /
                    <span id="numberofpages">0</span> )
                  </p>
                  <button
                    id="previous"
                    onClick={this.previousPage}
                    style={{ fontFamily: "Roboto-Medium" }}
                  >
                    Previous
                  </button>
                  <button
                    id="next"
                    onClick={this.nextPage}
                    style={{ fontFamily: "Roboto-Medium" }}
                  >
                    Next
                  </button>
                  <p style={{ float: "right", fontFamily: "Roboto-Regular" }}>
                    Row count : <span id="rowCount">0</span>
                  </p>
                </div>
              </div>
              {/* ----------------------------------------------------- */}
            </div>
          </div>
        </div>
        {/* Display modal to display error messages */}
        <div id="timeout_modal" className="modal">
          <div className="modal-content">
            <p id="content" style={{ textAlign: "center" }}></p>
            <button
              id="ok"
              className="btn-center btn success-btn"
              onClick={this.sessionTimeout}
            >
              OK
            </button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default DailyReport;
