import React, { Component, Fragment } from "react";
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";
import { linkClicked } from "../side_navigationbar/Navbar";
import "./Styling.css";

// Styling property for Widget Image size
const ImageSize = {
  width: "85%",
  height: "85%",
  margin: "0px",
  padding: "0px",
};

// Styling property for Underline Image
const Underline = {
  width: "75px",
  height: "9px",
  position: "absolute",
};

class Home extends Component {
  /** Method is called on Component Load */
  componentDidMount() {
    linkClicked(0);
  }

  /** Redern the html content on the browser */
  render() {
    return (
      <Fragment>
        <Helmet>
          <title>Home</title>
        </Helmet>
        <div className="panel">
          <span className="main-heading">HOME</span>
          <br />
          <img alt="" src="../images/Tiles/Underline.png" style={Underline} />
          <div className="container fading" style={{ marginTop: "50px" }}>
            <div className="row">
              {/* Widget for Configuration, on click navigate to Configuration page */}
              <div className="col-4">
                <Link to="/configuration">
                  <img
                    alt=""
                    src="../images/Widgets/Widget_Config.png"
                    style={ImageSize}
                    className="fading"
                    onClick={() => linkClicked(1)}
                  />
                </Link>
              </div>
              {/* Widget for Upload Floor Map, on click navigate to Upload Map page */}
              <div className="col-4">
                <Link to="/assets">
                  <img
                    alt=""
                    src="../images/Widgets/Widget_Allassets.png"
                    style={ImageSize}
                    className="fading"
                    onClick={() => linkClicked(2)}
                  />
                </Link>
              </div>
              {/* Widget for Tracking, on click navigate to Tracking page */}
              <div className="col-4">
                <Link to="/contacttracing">
                  <img
                    alt=""
                    src="../images/Widgets/Widget_Contacttracing.png"
                    style={ImageSize}
                    className="fading"
                    onClick={() => linkClicked(3)}
                  />
                </Link>
              </div>
              {/* Widget for All Assets, on click navigate to Assets page */}
              <div className="col-4">
                <Link to="/systemhealth">
                  <img
                    alt=""
                    src="../images/Widgets/Widget_Systemhealth.png"
                    style={ImageSize}
                    className="fading"
                    onClick={() => linkClicked(4)}
                  />
                </Link>
              </div>
              {/* Widget for Attendance, on click navigate to System Health page */}
              <div className="col-4">
                <Link to="/attendancesheet">
                  <img
                    alt=""
                    src="../images/Widgets/Widget_Attendance.png"
                    style={ImageSize}
                    className="fading"
                    onClick={() => linkClicked(5)}
                  />
                </Link>
              </div>
              {/* Widget for Daily Report, on click navigate to System Health page */}
              <div className="col-4">
                <Link to="/dailyreport">
                  <img
                    alt=""
                    src="../images/Widgets/Widget_Reports.png"
                    style={ImageSize}
                    className="fading"
                    onClick={() => linkClicked(6)}
                  />
                </Link>
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default Home;
