import React, { Component, Fragment } from "react";
import axios from "axios";
import "./Styling.css";
import $ from "jquery";
import { deleteGateway, registerGateway } from "../../urls/url";

class RegGateway extends Component {
  componentDidMount() {}

  /** Displays Delete master gateway form on clicking Delete Gateway Button */
  show = () => {
    $("#gatewayid").val();
    document.getElementById("gateway-del-form").style.display =
      document.getElementById("gateway-del-form").style.display === "block"
        ? "none"
        : "block";
  };

  /** Hides all error and success messages displayed on all button clicks */
  hide = () => {
    $("#master-error").text("");
    $("#master-success").text("");
  };

  registerGateway = (e) => {
    this.hide();
    e.preventDefault();
    let id = $("#gatewayid").val();
    let name = $("#gatewayname").val();
    if (id.length === 0) {
      $("#master-error").text("Please enter Gateway ID.");
    } else if (!id.match("([A-Za-z0-9]{2}[-]){5}([A-Za-z0-9]){2}")) {
      $("#master-error").text("Invalid Gateway ID entered.");
    } else {
      $("#master-error").text("");
      axios({
        method: "POST",
        url: registerGateway,
        data: { gatewayId: id, gatewayName: name },
      })
        .then((response) => {
          console.log(response);
          if (response.status === 201 || response.status === 200) {
            $("#master-success").text("Gateway registered successfully.");
          } else {
            $("#master-error").text("Unable to Register Gateway.");
          }
        })
        .catch((error) => {
          console.log(error);
          if (error.response.status === 403) {
            $("#timeout_modal4").css("display", "block");
            $("#content").text(
              "User Session has timed out. Please Login again"
            );
          } else {
            $("#master-error").text(
              "Request Failed with status code (" + error.response.status + ")."
            );
          }
        });
    }
  };

  unregisterGateway = (e) => {
    this.hide();
    e.preventDefault();
    let id = $("#gateway").val();
    if (id.length === 0) {
      $("#master-error").text("Please enter Gateway ID.");
    } else if (!id.match("([A-Za-z0-9]{2}[-]){5}([A-Za-z0-9]){2}")) {
      $("#master-error").text("Invalid Gateway ID entered.");
    } else {
      axios({
        method: "POST",
        url: deleteGateway,
        data: { gatewayId: id },
      })
        .then((response) => {
          console.log(response);
          if (response.status === 201 || response.status === 200) {
            $("#master-success").text("Gateway un-registered successfully.");
          } else {
            $("#master-error").text("Unable to un-register Gateway.");
          }
        })
        .catch((error) => {
          console.log(error);
          if (error.response.status === 403) {
            $("#timeout_modal4").css("display", "block");
            $("#content").text(
              "User Session has timed out. Please Login again"
            );
          } else {
            $("#master-error").text(
              "Request Failed with status code (" + error.response.status + ")."
            );
          }
        });
    }
  };

  /** Terminate the session on forbidden (403) error */
  sessionTimeout = () => {
    $("#timeout_modal4").css("display", "none");
    sessionStorage.removeItem("isLoggedIn");
    this.props.handleLoginError();
  };

  render() {
    return (
      <Fragment>
        <span className="sub-heading">Gateway Registration</span>
        <br />
        <img
          src="../images/Tiles/Underline.png"
          alt=""
          style={{
            width: "56px",
            height: "3px",
            marginTop: "2px",
            position: "absolute",
          }}
        />
        <br></br>
        {/* Elements for displaying error messages */}
        <div>
          <span className="error-msg" id="master-error"></span>
          <span className="success-msg" id="master-success"></span>
        </div>
        {/* Form to register master gateway */}
        <form id="gateway-reg-form">
          {/* Input field for Gateway ID */}
          <div className="input-group">
            <span className="label">Gateway ID :</span>
            <input
              type="text"
              name="gatewayid"
              id="gatewayid"
              required="required"
              placeholder="5a-c2-15-00-00-00"
              onChange={this.hide}
            />
          </div>
          {/* Select list for Floor names */}
          <div className="input-group">
            <span className="label">Gateway Name :</span>
            <input
              type="text"
              name="gatewayname"
              id="gatewayname"
              required="required"
              onClick={this.hide}
            />
          </div>
          <div className="input-group">
            <input
              type="submit"
              value="Register Gateway"
              onClick={this.registerGateway}
              className="btn success-btn"
            />
          </div>
        </form>
        {/* Button for toggeling for Deleting Gateway Form */}
        <button
          onClick={() => {
            this.show();
            this.hide();
            $(document).ready(function () {
              $(document).scrollTop($(document).height());
            });
          }}
          className="btn success-btn"
        >
          Remove Gateway
        </button>
        {/* Form for deleting the registered gateway tags */}
        <form
          id="gateway-del-form"
          className="fading"
          style={{ display: "none" }}
        >
          {/* Input Field for Tag MAC ID */}
          <div className="input-group">
            <span className="label">Gateway MAC ID :</span>
            <input
              type="text"
              name="gateway"
              id="gateway"
              required="required"
              onClick={this.hide}
              placeholder="5a-c2-15-00-00-00"
            />
          </div>
          {/* Elements for displaying error messages */}
          <div className="input-group">
            <input
              type="submit"
              value="Delete Gateway"
              onClick={this.unregisterGateway}
              className="btn success-btn"
            />
          </div>
        </form>
        {/* Display modal to display error messages */}
        <div id="timeout_modal4" className="modal">
          <div className="modal-content">
            <p id="content" style={{ textAlign: "center" }}></p>
            <button
              id="ok"
              className="btn-center btn success-btn"
              onClick={this.sessionTimeout}
            >
              OK
            </button>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default RegGateway;
